@import '../../../scss/breakpoints.scss';

.newsletter {
    background-color: var(--primary-color);
    color: var(--text-alternate-color);
    .newsletter-wrapper {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        @include to-md() {
            flex-direction: column;
        }
        .heading {
            display: flex;
            align-items: center;
            width: max-content;
            @include to-md() {
                margin: 0 auto;
            }
        }
        .form {
            flex: 1;
            .newsletter-form {
                position: relative;
                .field {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    gap: 20px;
                    flex: 1;
                    @include to-md() {
                        flex-direction: column;
                    }
                    .custom-checkbox {
                        display: flex;
                        align-items: center;
                        margin-bottom: 0;
                        max-width: 400px;
                        min-width: 200px;
                        @include to-md() {
                            margin: 0 auto;
                        }
                        @include to-width(589.98px) {
                            max-width: 310px;
                        }
                        @include to-width(379.98px) {
                            max-width: 200px;
                        }
                        p {
                            @include to-width(379.98px) {
                                font-size: 10px;
                            }
                        }
                        .checkmark {
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                    .field__input {
                        border-radius: 20px;
                        border: none;
                        min-width: 350px;
                        padding: 20px;
                        flex: 1;
                        box-sizing: border-box;
                        max-height: 40px;
                        @include to-md() {
                            padding: 12px 20px;
                        }
                        @include to-width(379.98px) {
                            min-width: unset;
                        }
                    }
                    button {
                        white-space: nowrap;
                        @include to-md() {
                            margin: 0 auto;
                        }
                    }
                    .field__label {
                        width: 0;
                        height: 0;
                        opacity: 0;
                        overflow: hidden;
                        position: absolute;
                        pointer-events: none;
                    }
                }
            }
        }
    }
    .newsletter-form__message {
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        .icon {
            width: 24px;
            height: 24px;
            background: #fff;
            border-radius: 50%;
            padding: 6px;
            margin-right: 15px;
            path {
                fill: var(--primary-color);
            }
        }
    }
}